.pagination {
    text-align: center;

    &__list {
        display: inline-flex;
        margin-inline: auto;
        column-gap: 22px;
    }

    &__button {
        width: 10px;
        aspect-ratio: 1;

        background-color: var(--border-color-gray);
        border-radius: 50%;

        

        &--active {
            background-color: var(--text-color);
        }
    }
}